@charset "UTF-8";
/* 共通設定 */
html body {
  height: 100%;
}

/* 共通バー */
/* トップロゴ */
.top_logo {
  position: relative;
}
.top_logo p {
  position: absolute;
  color: rgba(0, 0, 0, 0);
  letter-spacing: 0.1em;
  -webkit-text-stroke: 0.5px #d1c146;
  text-shadow: 2px 2px 0 #7fcac3;
  font-weight: bold;
  font-size: 2.5rem;
  top: 50%;
  left: 60%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
}
.top_logo img {
  display: block;
  width: 80%;
  border-radius: 1em;
  margin: 2em auto;
}

/* リンク */
.link {
  width: 80%;
  margin: 1em auto;
  padding-top: 2em;
}

.link_back {
  position: absolute;
  bottom: 1rem;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
}

a.btn-border {
  display: block;
  padding: 1rem;
  border: 0.2em dashed #ffcdd2;
  border-radius: 0.5rem 0.5rem 0.5rem 0.5rem;
  text-decoration: none;
  text-align: center;
  color: #ffcdd2;
  font-weight: bold;
  font-size: 2em;
}

